import _ from './utils';
import {
  REPLACE, REORDER, PROPS, TEXT
} from './patch';
import listDiff from 'list-diff2';

// 
const diff = (oldTree, newTree) => {
  let index = 0;    // 
  let patches = {};   // 
  difWalk(oldTree, newTree, index, patches);
  return patches;
}

// 
const difWalk = (oldNode, newNode, index, patches) => {
  let currentPatch = [];

  if (newNode === null) {   //  | Node is removed
    // DOM
    // Real DOM node will be removed when perform reordering, so has no needs to do anthings in here
  } else if (
    _.isString(oldNode) &&
    _.isString(newNode)
  ) {    // TextNode  | TextNode content replacing
    if (newNode !== oldNode) {
      currentPatch.push({
        type: TEXT,
        content: newNode
      });
    }
  } else if (
    oldNode.tagName === newNode.tagName &&
    oldNode.key === newNode.key
  ) {   //  | Nodes are the same, diff old node's props and children
    // props
    let propsPatches = diffProps(oldNode, newNode);
    if (propsPatches) {
      currentPatch.push({
        type: PROPS,
        props: propsPatches
      });
    }
    // children
    if (!isIgnoreChildren(newNode)) {   //  `ignore`  | If the node has a `ignore` property, do not diff children
      diffChildren(
        oldNode.children,
        newNode.children,
        index,
        patches,
        currentPatch
      );
    }
  } else {    //  | Nodes are not the same, replace the old node with new node
    currentPatch.push({type: REPLACE, node: newNode});
  }

  if (currentPatch.length) {
    patches[index] = currentPatch;
  }
} 

const diffChildren = (oldChildren, newChildren, index, patches, currentPatch) => {
  let diffs = listDiff(oldChildren, newChildren, 'key');
  newChildren = diffs.children;

  if (diffs.moves.length) {
    let reorderPatch = {type: REORDER, moves: diffs.moves};
    currentPatch.push(reorderPatch);
  }

  let leftNode = null,
    currentNodeIndex = index;
  _.each(oldChildren, function (child, i) {
    let newChild = newChildren[i];
    currentNodeIndex = (leftNode && leftNode.count)
      ? currentNodeIndex + leftNode.count + 1
      : currentNodeIndex + 1;
    difWalk(child, newChild, currentNodeIndex, patches);
    leftNode = child;
  });
}

const diffProps = (oldNode, newNode) => {
  let key, value, 
    count = 0,
    oldProps = oldNode.props, 
    newProps = oldNode.props,
    propsPatches = {};

  //  | Find out different properties
  for (key in oldProps) {
    value = oldProps[key];
    if (newProps[key] !== value) {
      count++;
      propsPatches[key] = newProps[key];
    }
  }

  //  | Find out new property
  for (key in newProps) {
    value = newProps[key];
    if (!oldProps.hasOwnProperty(key)) {
      count++;
      propsPatches[key] = newProps[key];
    }
  }

  if (count === 0) {    //  | If properties all are identical
    return null;
  }
  return propsPatches;
}

const isIgnoreChildren = node => (
  node.props && node.props.hasOwnProperty('ignore')
)

export default diff;